@media (max-width: 767.98px) {
  .header__container, .menu__list {
    column-gap: 15px;
  }

  .icon-menu {
    height: 18px;
    z-index: 5;
    flex: 0 0 30px;
    display: block;
    position: relative;
  }

  .icon-menu span, .icon-menu:before, .icon-menu:after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: all .3s;
    position: absolute;
    right: 0;
  }

  .icon-menu:before {
    top: 0;
  }

  .icon-menu:after {
    bottom: 0;
  }

  .icon-menu span {
    top: calc(50% - 1px);
  }

  .menu-open .icon-menu span {
    width: 0;
  }

  .menu-open .icon-menu:before {
    top: calc(50% - 1px);
    transform: rotate(-45deg);
  }

  .menu-open .icon-menu:after {
    bottom: calc(50% - 1px);
    transform: rotate(45deg);
  }

  .menu__body {
    width: 100%;
    height: 100%;
    background-color: #000;
    padding: 100px 15px 30px;
    transition: left .3s;
    position: fixed;
    top: 0;
    left: -100%;
    overflow: auto;
  }

  .menu__body:before {
    content: "";
    width: 100%;
    height: 70px;
    z-index: 2;
    background-color: #000;
    transition: left .3s;
    position: fixed;
    top: 0;
    left: -100%;
  }

  .menu__list {
